.SearchResults {
	width: 50%;
	height: 400px;
	overflow-y: scroll;
	padding: 2.27rem .88rem;
	background-color: rgba(1, 12, 63, 0.7);
	box-shadow: 2px 2px 2px 2px #000000; }
::-webkit-scrollbar {
	width: 0px;
	background: transparent; }

@media only screen and ( max-width:  1020px) {
    .SearchResults {
        width: 90%;
        margin-bottom: 2rem; } }

